import { useEffect, useState } from "react";
import { financeLocalforage } from "../utils/localforage";
import { trans1 } from "../utils/trans";
import localforage from "localforage";

export type financeItem = {
  end_date: string;
  roic: number;
  eps: number; // 
  roe: number;
  grossprofit_margin: number; // 
  netprofit_margin: number; // 
  ar_turn: number; // 
  inv_turn: number; // 
  assets_turn: number; // 
  debt_to_assets: number; // 
};

const initFinance = async () => {
  const fields = (await localforage.getItem("financeFields")) as string[];
  const data: Record<string, any[]> = {};
  await financeLocalforage.iterate((value: any[][], key: string) => {
    const item = trans1({ fields, items: value }, [
      "end_date",
      "roic",
      "eps",
      "roe",
      "grossprofit_margin",
      "netprofit_margin",
      "ar_turn",
      "inv_turn",
      "assets_turn",
      "debt_to_assets",
    ]);
    data[key] = item;
  });
  return data;
};

let locked = false;
const useFinance = () => {
  const [financeData, setFinanceData] = useState<Record<string, financeItem[]>>(
    {}
  );
  useEffect(() => {
    if (!locked) {
      locked = true;
      initFinance().then((res) => {
        setFinanceData(res);
      });
    }
  }, []);
  return { financeData };
};

export default useFinance;
